@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@500&display=swap');

:root {
  --pri: #000;
  --sec: #0365FF;
  --ter: #fff;
  --shadow: rgb(29, 29, 29);
}

body {
  margin: 0;
  font-family: 'Poppins', sans-serif;
  color: var(--ter);
  background-color: var(--pri);
  font-size: 20px;
}

h2, h3, h4 {
  font-weight: 500;
  margin: 0;
}

h1 {
  font-size: 3em;
  margin: 0 0 0 -2.5px;
  color: var(--sec);
}

h2 {
  font-size: 1.5em;
}

h3 {
  font-size: 1em;
}

h4 {
  font-size: 0.7em;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
}

.home, .other, .project, .contact {
  display: grid;
  padding: 15px;
  gap: 30px;
  margin-bottom: 20px;
}

.home, .contact {
  align-items: center;
  grid-template-columns: repeat(2, 1fr);
}

.other .box h2, .project a {
  color: var(--sec);
}

.home {
  min-height: 100vh;
  box-sizing: border-box;
}

.home .box img {
  width: 5em;
}

.home .profile img {
  width: 100%;
  display: block;
}

.other {
  gap: 15px !important;
  grid-template-columns: repeat(4, 1fr);
}

.other .box {
  background-color: var(--shadow);
  border-radius: 3px;
  padding: 7px 15px;
}

.other .box .icon {
  margin-right: 7px;
}

.list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 10px;
}

.image {
  height: 250px;
  position: relative;
  background-color: var(--shadow);
}

.image img{
  width: 100%;
  height: 100%;
  border-radius: 3px;
  filter: brightness(75%);
}

.image .text {
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  padding: 11px 15px;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  box-sizing: border-box;
  box-shadow: 0 -99px 75px -65px var(--shadow) inset;
}

.contact .box h3 {
  margin-bottom: 20px !important;
}

.social {
  display: flex;
  gap: 10px;
}

.social a {
  font-size: 1.7em;
  background-color: var(--sec);
  color: var(--ter);
  line-height: 100%;
  padding: 5px 7px;
  border-radius: 3px;
}

form {
  display: grid;
}

form input, form textarea {
  outline: none;
  background-color: transparent;
  border-radius: 3px;
  border: 1px solid var(--sec);
  padding: 10px;
  font-size: 0.7em;
  font-family: 'Poppins', sans-serif;
  color: var(--ter);
  resize: none;
  margin: 5px 0 15px;
}

form button {
  padding: 10px;
  background-color: var(--sec);
  border-radius: 3px;
  border: none;
  outline: none;
  cursor: pointer;
  font-size: 1em;
  color: var(--ter);
}

.footer {
  display: grid;
  padding: 15px;
  gap: 5px;
}

.dis {
  filter: brightness(75%);
}

.alert {
  position: fixed;
  top: 5px;
  right: 5px;
  padding: 0 10px;
  background-color: var(--sec);
  border-radius: 3px;
  z-index: 99;
}

@media screen and (max-width: 750px) {
  body {
    font-size: 15px;
  }

  .home, .contact {
    grid-template-columns: repeat(1, 1fr);
  }

  .list, .other {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media screen and (max-width: 500px) {
  .list, .other {
    grid-template-columns: repeat(1, 1fr);
  }
}
